@import "library";

.herobanner-bg {
  background-color: #f5f5f5;
}

.herobanner {
  background-image: url("../images/payshepherd/p-herobanner3.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 500px; /* Adjust the initial height as needed */
  text-align: center;
  // display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 350px) {
  .herobanner {
    max-height: 200px; /* Adjust the height for larger screens */
  }
}
@media (min-width: 768px) {
  .herobanner {
    max-height: 300px; /* Adjust the height for larger screens */
  }
}
@media (min-width: 900px) {
  .herobanner {
    max-height: 400px; /* Adjust the height for larger screens */
  }
}
@media (min-width: 1400px) {
  .herobanner {
    width: 70%;
    margin: 0 auto;
    max-height: 450px; /* Adjust the height for larger screens */
  }
}

.bg1 {
  background-color: #fcf9f2;
}

hr {
  border-top: 1px solid $grey-stroke;
  margin-bottom: $space-subtitle-bottom;
  // padding-top: $space-content-bottom;
}

.intro,
.challenge,
.research,
.problem,
.solution,
.ui,
.solved,
.learning,
.ending {
  padding-top: $space-content-top;
  padding-bottom: $space-content-bottom;
}
.learning-text {
  background-color: #0f253a;
  color: white;
  padding: 40px 100px 0 100px;
  // margin: 0 10px;
}
.learning-img {
  // background-color: #0f253a;
  background: linear-gradient(
    to top,
    #f3f3f3,
    #f3f3f3 50%,
    #f3f3f3 61%,
    #0f253a 61%
  );
}
.problem2-panel {
  border-radius: 10px;
  background-color: #f3f3f3;
  filter: drop-shadow(0 2px 3px rgb(0, 0, 0, 0.25));
  padding: $space-panel;
  margin-top: 16px;
  margin-bottom: 16px;
}
.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.wrong-icon {
  background: url("../images/payshepherd/wrong-icon.png") no-repeat left top;
  //   height: 80px;
  padding-left: 44px;
  padding-bottom: 24px;
}

.panel-bg {
  border-radius: 10px;
  background-color: white;
  filter: drop-shadow(0 2px 3px rgb(0, 0, 0, 0.25));
  padding: $space-panel;
  margin-top: 16px;
  margin-bottom: 16px;
}

.primary-btn {
  background-color: #286cb2;
  border-radius: 4px;
  text-align: center;
  height: 40px;
  padding: 4px;
  color: white;
}

.secondary-btn {
  border: 1px solid $grey-stroke;
  border-radius: 4px;
  height: 40px;
  padding: 4px;
  text-align: center;
}

.third-btn {
  background-color: #00a624;
  border-radius: 4px;
  text-align: center;
  height: 40px;
  padding: 4px;
  color: white;
}

.link {
  color: #286cb2;
  font-weight: 600;
}

.icon-title {
  font-size: 0.8rem;
  font-weight: 600;
}

.footer {
  padding-bottom: $space-ending;
}
